.navbar {
  color: black;
  height: 80px;
  padding: 20px;
  color: #fff;
  font-family: "Baloo Tamma 2", cursive;
  z-index: 100;
  border-radius: 0px;
  border: none; }

.navbar-brand {
  position: absolute;
  font-family: "Baloo Tamma 2", cursive;
  display: inline-flex; }

.navbar-toggler {
  position: absolute;
  right: 20px; }

.navbar_logo {
  width: 50px;
  height: 50px;
  margin-right: 10px;
  padding-bottom: 10px; }

.navbar_text {
  color: black;
  font-size: 14px;
  font-family: "Baloo Tamma 2", cursive;
  display: flex; }

.nav-item a {
  color: black !important;
  transition: 1s ease-in-out;
  color: #fff;
  font-size: 18px;
  font-family: "Comic Neue", cursive;
  font-weight: bold;
  top: 5px;
  padding: 1px 10px;
  line-height: 20px; }
  .nav-item a:hover {
    color: coral;
    transition: 1s ease-in-out;
    text-decoration: none; }

.nav_link-active {
  transition: 1s ease-in-out;
  border-bottom: 1px solid coral; }

.navbar-toggler-icon span {
  width: 100%;
  height: 1px;
  margin: 0px;
  background: black; }

/*
// .footer-contacts
*/
.footer-contacts__title {
  margin-bottom: 25px;
  font-family: "Baloo Tamma 2", cursive; }

.footer-contacts__text {
  font-size: 15px;
  line-height: 22px;
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Baloo Tamma 2", cursive; }
  [dir=ltr] .footer-contacts__text {
    padding-right: 20px; }
  [dir=rtl] .footer-contacts__text {
    padding-left: 20px; }
  .footer-contacts__text a {
    color: inherit; }

.footer-contacts__contacts {
  font-size: 15px;
  line-height: 20px;
  list-style: none;
  padding: 0;
  margin: 16px 0 0; }
  .footer-contacts__contacts a {
    color: inherit;
    font-family: "Baloo Tamma 2", cursive; }
  .footer-contacts__contacts li {
    padding: 4px 0;
    position: relative; }

.footer-contacts__icon {
  display: inline-block;
  text-align: center;
  width: 22px; }
  [dir=ltr] .footer-contacts__icon {
    margin-right: 2px; }
  [dir=rtl] .footer-contacts__icon {
    margin-left: 2px; }

@media (max-width: 767px) {
  .footer-contacts {
    text-align: center; }
  .footer-contacts__title {
    font-size: 28px;
    margin-bottom: 16px; }
  .footer-contacts__text {
    padding: 0; } }

/*
// .footer-links
*/
.footer-links__title {
  margin-bottom: 22px;
  font-family: "Baloo Tamma 2", cursive; }

.footer-links__list {
  font-size: 15px;
  line-height: 28px;
  list-style: none;
  padding: 0;
  margin: 0; }
  .footer-links__list a {
    color: inherit;
    font-family: "Baloo Tamma 2", cursive;
    transition: .15s; }
  .footer-links__list a:hover {
    color: coral; }

@media (max-width: 767px) {
  .footer-links__title {
    margin-bottom: 12px; }
  .footer-links {
    margin-top: 42px;
    text-align: center; } }

/*
// .footer-newsletter
*/
.footer-newsletter__title {
  margin-bottom: 25px; }

.footer-newsletter__text {
  font-size: 15px;
  line-height: 22px;
  margin-bottom: 14px; }

.footer-newsletter__text--social {
  margin-top: 20px;
  margin-bottom: 0; }

.footer-newsletter__social-links {
  display: flex;
  margin: 8px 0 0;
  padding: 0;
  list-style: none; }

[dir=ltr] .footer-newsletter__social-link + .footer-newsletter__social-link {
  margin-left: 7px; }

[dir=rtl] .footer-newsletter__social-link + .footer-newsletter__social-link {
  margin-right: 7px; }

.footer-newsletter__social-link a {
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: inherit;
  width: 34px;
  height: 34px;
  border-radius: 17px;
  transition: all .2s; }

.footer-newsletter__social-link a:hover {
  opacity: .8; }

.footer-newsletter__social-link a {
  color: #fff; }

.footer-newsletter__social-link--facebook a {
  background: #3c5a99; }

.footer-newsletter__social-link--twitter a {
  background: #00a2e8; }

.footer-newsletter__social-link--youtube a {
  background: #e52e2e; }

.footer-newsletter__social-link--instagram a {
  background: #815dc7; }

.footer-newsletter__social-link--rss a {
  background: #ffc338; }

.footer-newsletter__form {
  display: flex;
  max-width: 380px; }

[dir=ltr] .footer-newsletter__form-button {
  margin-left: 6px; }

[dir=rtl] .footer-newsletter__form-button {
  margin-right: 6px; }

@media (max-width: 991px) {
  .footer-newsletter {
    margin-top: 42px;
    max-width: 420px; }
  .footer-newsletter__title {
    margin-bottom: 16px; } }

@media (max-width: 767px) {
  .footer-newsletter {
    text-align: center; }
    [dir=ltr] .footer-newsletter {
      margin-left: auto;
      margin-right: auto; }
    [dir=rtl] .footer-newsletter {
      margin-right: auto;
      margin-left: auto; }
  .footer-newsletter__title {
    font-size: 28px; }
  .footer-newsletter__form {
    margin: 0 auto; }
  .footer-newsletter__social-links {
    margin-top: 16px;
    justify-content: center; } }

/*
// .site-footer
*/
.site-footer {
  background: #000F26;
  border-top: 1px solid #ebebeb;
  font-family: "Baloo Tamma 2", cursive;
  margin-top: 5px; }

.site-footer__widgets {
  padding: 48px 0 46px; }

.site-footer__bottom {
  height: 54px;
  border-top: 1px solid #ebebeb;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.site-footer__copyright {
  font-size: 14px;
  font-family: "Baloo Tamma 2", cursive; }
  .site-footer__copyright a:hover {
    text-decoration: underline; }

.site-footer__payments img {
  max-width: 100%; }

.site-footer__widget + .site-footer__widget {
  margin-top: 24px; }

@media (max-width: 767px) {
  .site-footer__widgets {
    padding-bottom: 48px; }
  .site-footer__bottom {
    padding: 20px 0 24px;
    flex-wrap: wrap;
    height: auto; }
  .site-footer__payments {
